import React, {Component} from 'react';
import {connect} from 'react-redux';
import { Panel, PanelHeader, PanelHeaderClose, Header, CellButton } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import * as vkSelectors from '../store/vk/reducer';
import * as currencyRatesActions from '../store/currency_rates/actions';

import logoMain from '../image/paw.png';

class CityPanel extends Component {

    componentWillMount() {
        this.props.dispatch(currencyRatesActions.fetchUsdEurRate());
        this.props.dispatch(currencyRatesActions.fetchCbrCurrencyRates());
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
               <PanelHeader>
                    <div style={{display: 'flex', alignItems: 'center'}}>
                        <img onClick={this.openMain} width="32" src={logoMain} />&nbsp;&nbsp;&nbsp;Лапы и хвосты
                    </div>
                </PanelHeader>
                <Header>Города</Header>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(CityPanel);
